import React, { useEffect, useState, useRef } from "react";

import {
  AppBar,
  Tabs,
  Tab,
  Typography,
  Box,
  Grid,
  Container,
  TextField,
  Paper,
} from "@material-ui/core";
import axios from "axios";

import SingleResult from "./SingleResult";

import "./styles.css";
import { API_URL } from "../../../../utils/Api";
import {
  SerachResultObjectProps,
  SerachResultProps,
} from "../../../../utils/typeHelpers";

import { useLocation } from "react-router";
interface TabPanelProps {
  children?: React.ReactNode;
  index: any;
  value: any;
}

function TabPanel(props: TabPanelProps) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`nav-tabpanel-${index}`}
      aria-labelledby={`nav-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box p={3}>
          <Typography>{children}</Typography>
        </Box>
      )}
    </div>
  );
}

function a11yProps(index: any) {
  return {
    id: `nav-tab-${index}`,
    "aria-controls": `nav-tabpanel-${index}`,
  };
}

interface LinkTabProps {
  label?: string;
  href?: string;
}

function LinkTab(props: LinkTabProps) {
  return (
    <Tab
      component="a"
      onClick={(event: React.MouseEvent<HTMLAnchorElement, MouseEvent>) => {
        event.preventDefault();
      }}
      {...props}
    />
  );
}
interface RouteProps {
  searchTerm: string;
}

export default function Search() {
  const { state } = useLocation();
  const val = state as RouteProps;

  const [value, setValue] = useState(0);
  const [searchTerm, setSearchTerm] = useState(
    val.searchTerm ? val.searchTerm : ""
  );
  let [data, setData] = useState<SerachResultObjectProps>({
    all: [],
    donor: [],
    donee: [],
    ngo: [],
    goals: [],
  });

  const handleChange = (event: React.ChangeEvent<{}>, newValue: number) => {
    setValue(newValue);
  };

  useEffect(() => {
    let source = axios.CancelToken.source();

    const loadData = async () => {
      try {
        const response = await axios.get(
          `${API_URL}/search/?query=${searchTerm}`,
          {
            cancelToken: source.token,
            headers: {
              Accept: "application/json",
              "Content-Type": "application/json",
            },
          }
        );
        const donorList = response.data.search_result.filter(
          (item: SerachResultProps) => item.type === "USER"
        );
        const doneeList = response.data.search_result.filter(
          (item: SerachResultProps) => item.type === "DONEE"
        );
        const ngoList = response.data.search_result.filter(
          (item: SerachResultProps) => item.type === "NGO"
        );
        const goalList = response.data.search_result.filter(
          (item: SerachResultProps) => item.type === "GOAL"
        );

        setData({
          ...data,
          all: response.data.search_result,
          donor: donorList,
          donee: doneeList,
          ngo: ngoList,
          goals: goalList,
        });
      } catch (error) {
        if (axios.isCancel(error)) {
        } else {
          throw error;
        }
      }
    };
    loadData();

    return () => {
      source.cancel();
    };
  }, [searchTerm]);

  return (
    <Container>
      <div className="search">
        <Grid container spacing={3} sx={{ padding: 4 }}>
          <Grid item md={10}>
            <Paper>
              <div>
                <form className="search-form" noValidate autoComplete="off">
                  <TextField
                    value={searchTerm}
                    onChange={(e) => setSearchTerm(e.target.value)}
                    id="Search"
                    label="Search any keyword"
                    variant="outlined"
                    fullWidth
                  />
                </form>
              </div>

              <AppBar position="static">
                <Tabs
                  style={{ backgroundColor: "white", borderRadius: 10 }}
                  variant="fullWidth"
                  value={value}
                  onChange={handleChange}
                  aria-label="nav tabs example"
                >
                  <LinkTab label="All" href="/all" {...a11yProps(0)} />
                  <LinkTab label="Donor" href="/doner" {...a11yProps(1)} />
                  <LinkTab label="Donee" href="/donee" {...a11yProps(2)} />

                  <LinkTab label="NGO" href="/ngo" {...a11yProps(3)} />
                  <LinkTab label="Goals" href="/goals" {...a11yProps(4)} />
                </Tabs>
              </AppBar>

              <TabPanel value={value} index={0}>
                {data.all.length > 0 ? (
                  data.all.map((item: SerachResultProps) => (
                    <SingleResult
                      uid={item.uid}
                      title={item.title}
                      type={item.type}
                      img={item.img}
                    />
                  ))
                ) : (
                  <Typography>
                    Your search - {searchTerm} - did not match any documents.
                  </Typography>
                )}
              </TabPanel>
              <TabPanel value={value} index={1}>
                {data.donor.length > 0 ? (
                  data.donor.map((item: SerachResultProps) => (
                    <SingleResult
                      uid={item.uid}
                      title={item.title}
                      type={item.type}
                      img={item.img}
                    />
                  ))
                ) : (
                  <Typography>
                    Your search - {searchTerm} - did not match any documents.
                  </Typography>
                )}
              </TabPanel>
              <TabPanel value={value} index={2}>
                {data.donee.length > 0 ? (
                  data.donee.map((item: SerachResultProps) => (
                    <SingleResult
                      uid={item.uid}
                      title={item.title}
                      type={item.type}
                      img={item.img}
                    />
                  ))
                ) : (
                  <Typography>
                    Your search - {searchTerm} - did not match any documents.
                  </Typography>
                )}
              </TabPanel>
              <TabPanel value={value} index={3}>
                {data.ngo.length > 0 ? (
                  data.ngo.map((item: SerachResultProps) => (
                    <SingleResult
                      uid={item.uid}
                      title={item.title}
                      type={item.type}
                      img={item.img}
                    />
                  ))
                ) : (
                  <Typography>
                    Your search - {searchTerm} - did not match any documents.
                  </Typography>
                )}
              </TabPanel>
              <TabPanel value={value} index={4}>
                {data.goals.length > 0 ? (
                  data.goals.map((item: SerachResultProps) => (
                    <SingleResult
                      uid={item.uid}
                      title={item.title}
                      type={item.type}
                      img={item.img}
                    />
                  ))
                ) : (
                  <Typography>
                    Your search - {searchTerm} - did not match any documents.
                  </Typography>
                )}
              </TabPanel>
            </Paper>
          </Grid>
        </Grid>
      </div>
    </Container>
  );
}
